        methods: {
            tFormat(){
                this.timeList = [];
                let x = moment(new Date()).day(1).format("MMDD"),
                    y = moment(new Date()).day(7).format("MMDD"),
                    nX = moment(new Date()).day(1).format("YYYYMMDD"),
                    nY = moment(new Date()).day(7).format("YYYYMMDD");
                for(let i = 1; i < 8; i++){
                    this.weekList.push(moment(new Date()).day(i).format('MMDD'));
                    this.timeList.push(moment(new Date()).day(i).format('x'));
                }
                this.week.one = x;
                this.week.seven = y;
                this.week.nOne = nX;
                this.week.nSeven = nY;

            },
            changeWk(val){
                if(val == 'add'){
                    this.weekNum --;
                    this.getWeek(this.weekNum);
                } else if(val == 'zero'){
                    this.weekNum = 0;
                    this.getWeek(this.weekNum)
                }else{
                    this.weekNum ++;
                    this.getWeek(this.weekNum)
                }
                this.getFBook();
                this.iBtn = true;
                this.isModify = false;
            },
            getWeek(val){
                this.weekList = [];
                let x = val * 7 * 24 * 60 * 60 *1000,
                    y = x + 6 * 24 * 60 * 60 *1000,
                    nX = val * 7 * 24 * 60 * 60 *1000,
                    nY = nX + 6 * 24 * 60 * 60 *1000,
                    t = 0;

                for(let i = 1; i < 8; i++){
                    let s = t * 24 * 60 * 60 *1000;
                    t++;
                    let ss = moment(parseInt(moment(new Date()).day(1).format("x")) + x + s).format('MMDD');
                    this.weekList.push(ss);
                }
                x = moment(parseInt(moment(new Date()).day(1).format("x")) + x).format('MMDD');
                nX = moment(parseInt(moment(new Date()).day(1).format("x")) + nX).format('YYYYMMDD');
                y = moment(parseInt(moment(new Date()).day(1).format("x")) + y).format('MMDD');
                nY = moment(parseInt(moment(new Date()).day(1).format("x")) + nY).format('YYYYMMDD');
                this.week.one = x;
                this.week.seven = y;
                this.week.nOne = nX;
                this.week.nSeven = nY;
            },
            edit(val){
                if(val=='e'){
                    if(this.isNew){
                        this.isE = false;
                        this.notData[0].name = [];
                        this.notData.forEach((e,i)=>{
                            e.name = [[],[],[],[],[],[],[]];
                        });
                        this.dataSource = this.notData;
                        this.columns = this.hasTab;

                        let time = moment(new Date()).format("x");
                        this.isExpire = [true,true,true,true,true,true,true];
                        this.timeList.forEach((e,i) =>{
                            if(e <= time){
                                this.isExpire[i] = false;
                            }
                        });
                    }
                    this.iBtn = !this.iBtn;
                    this.isModify = !this.isModify;
                }else if(val == 'u'){
                    if(this.btnState == ''){
                        this.visible3 = !this.visible3
                        this.v3Title = ''
                    }else if(this.btnState == ''){
                        this.visible3 = !this.visible3
                        this.btnTxt =''
                        this.v3Title = ''
                    }else if(this.btnState == ''){
                        this.visible3 = !this.visible3
                        this.btnTxt =''
                        this.v3Title = ''
                    }else{
                        this.visible3 = !this.visible3
                        this.btnTxt =''
                        this.v3Title = ''
                    }
                }else{
                    this.visible4 = !this.visible4
                }
            },
            editB(val){
                if(val=='f'){
                    let list = [];
                    this.dataSource.forEach((e,i)=>{
                        let x = 0;
                        switch (i) {
                            case 0:
                                x = 1;
                                break;
                            case 1:
                                x = 2;
                                break;
                            case 2:
                                x = 3;
                                break;
                            case 3:
                                x = 4;
                        }
                        e.name.forEach((e2,i2)=>{
                            let week = 0
                            switch (i2) {
                                case 0:
                                    week = 1;
                                    break;
                                case 1:
                                    week = 2;
                                    break;
                                case 2:
                                    week = 3;
                                    break;
                                case 3:
                                    week = 4;
                                    break;
                                case 4:
                                    week = 5;
                                    break;
                                case 5:
                                    week = 6;
                                    break;
                                case 6:
                                    week = 7;
                            }
                            e2.forEach((e3)=>{
                                let obj = {
                                    week:week,
                                    meal_times:x,
                                    food_id:e3.food_id || e3.id
                                };
                                list.push(obj)
                            })
                        });
                    });
                    if(this.isModify && list.length > 0){
                        this.state = false;
                    }else{
                        this.state = true;
                        this.notData[0].name[0] = '';
                        this.dataSource = this.notData;
                        this.columns = this.notTab;
                    }
                    this.isModify = !this.isModify;
                    this.iBtn = !this.iBtn;
                }else if(val == 'p'){
                    if(this.btmTxt.dietitian == ''){
                        this.col = true;
                        this.$message.warning(``);
                    }else{
                        let list = [];
                        this.dataSource.forEach((e,i)=>{
                            let x = 0;
                            switch (i) {
                                case 0:
                                    x = 1;
                                    break;
                                case 1:
                                    x = 2;
                                    break;
                                case 2:
                                    x = 3;
                                    break;
                                case 3:
                                    x = 4;
                            }
                            e.name.forEach((e2,i2)=>{
                                let week = 0
                                switch (i2) {
                                    case 0:
                                        week = 1;
                                        break;
                                    case 1:
                                        week = 2;
                                        break;
                                    case 2:
                                        week = 3;
                                        break;
                                    case 3:
                                        week = 4;
                                        break;
                                    case 4:
                                        week = 5;
                                        break;
                                    case 5:
                                        week = 6;
                                        break;
                                    case 6:
                                        week = 7;
                                }
                                e2.forEach((e3)=>{
                                    let obj = {
                                        week:week,
                                        meal_times:x,
                                        food_id:e3.food_id || e3.id
                                    };
                                    list.push(obj)
                                })
                            });
                        });
                        let data = {
                            url:'api/school/recipes/'+ this.week.nOne + this.week.nSeven,
                            method:'put',
                            data:{
                                id:this.ctnKey,
                                producer:this.btmTxt.producer,
                                dietitian:this.btmTxt.dietitian,
                                foods:JSON.stringify(list)
                            }
                        };
                        this.$apiAxiox(data).then((res)=>{
                            console.log(res);
                            if(res.data.code == 0){
                                this.$message.success(``);
                                this.iBtn = !this.iBtn;
                                this.isModify = !this.isModify;
                            }else{
                                this.$message.error(res.data.msg);
                            }
                        });

                    }
                }else{
                    this.notData[0].name[0] = '';
                    this.dataSource = this.notData;
                    this.columns = this.notTab;
                    this.$message.success(``);
                }
            },
            //
            addCk(val,i,val2){
                this.foodI = i;
                this.foodL = val;
                if(val2.logo == ''){
                    this.fNum = 0;
                }else if(val2.logo == ''){
                    this.fNum = 1;
                }else if(val2.logo == ''){
                    this.fNum = 2;
                }else{
                    this.fNum = 3;
                };
                switch (i) {
                    case 1:
                        this.oWeek = "";
                        break;
                    case 2:
                        this.oWeek = "";
                        break;
                    case 3:
                        this.oWeek = "";
                        break;
                    case 4:
                        this.oWeek = "";
                        break;
                    case 5:
                        this.oWeek = "";
                        break;
                    case 6:
                        this.oWeek = "";
                        break;
                    case 7:
                        this.oWeek = "";
                }

                this.dayWeek = this.weekList[i-1];
//                this.fcList = [];
                this.addCK = !this.addCK;
                this.seachFood(0);
            },
            //
            reFood(val,i,index){
                val[index-1].splice(i,1);
            },
            //
            seachFood(val){
                let type = 0;
                if(val==0){
                    type = 0
                }else{
                    type = val;
                }

                let x = this.btnI + 1;
                let data = {
                    method:'get',
//                    url:'api/school/dishes?keyword=' + this.foodName + '&state=' + this.btnI +'&type=' + this.sBtnI + '&page=' + this.pKey,
                    url:'api/school/dishes?keyword=' + this.foodName + '&state=' + x +'&type=' + type + '&page=' + this.pKey,
                };

                this.$apiAxiox(data).then((res)=>{
                    if(res.data.data.data.length == 0){
                        this.hasFod = true;
                        this.seachS = true;
                    }else{
                        this.hasFod = false;
                        this.fodList = [];
                        this.pTotal = res.data.data.total;
                        res.data.data.data.forEach((e,i) =>{
                            if(e.cover == ''){
                                e.cover = '../../assets/login/logo.png'
                            }
                            if(e.last_use == 0){
                                e.last_use = moment(new Date()).format('YYYY-MM-DD');
                            }
                            let x = false;
                            if(this.fcList.length != 0){
                                this.fcList.forEach((e1,i)=>{
                                    if(e1.id == e.id){
                                        x = true;
                                    }
                                })
                            };
                            let obj = {
                                id:e.id,
                                state:x,
                                url:e.cover,
                                title:e.title,
                                num:e.material_num,
                                time:e.last_use,
                                excipient:e.excipient,
                                materials:e.materials
                            };
                            this.fodList.push(obj);
                        });
                        console.log(this.fodList);
                        this.fodList.forEach((e,i)=>{
                            this.foodL.forEach((e2,i2)=>{
                                if(e.id == e2.id){
                                    e.state = true;
                                }
                            })
                        });
                    }
//                    if(res.data.code == 0){
//                        console.log(res.data.data);
//                    }else{
//                        this.$message.warning(res.data.msg);
//                    }

                })

            },
            InpTxt(){
               if(this.Dietitian !=''){
                   this.col = false;
               }
            },
            rChange(e){
                if(this.notData[0].name[0] != ''){
                    let list = [];
                    this.dataSource.forEach((e,i)=>{
                        let x = 0;
                        switch (i) {
                            case 0:
                                x = 1;
                                break;
                            case 1:
                                x = 2;
                                break;
                            case 2:
                                x = 3;
                                break;
                            case 3:
                                x = 4;
                        }
                        e.name.forEach((e2,i2)=>{
                            let week = 0
                            switch (i2) {
                                case 0:
                                    week = 1;
                                    break;
                                case 1:
                                    week = 2;
                                    break;
                                case 2:
                                    week = 3;
                                    break;
                                case 3:
                                    week = 4;
                                    break;
                                case 4:
                                    week = 5;
                                    break;
                                case 5:
                                    week = 6;
                                    break;
                                case 6:
                                    week = 7;
                            }
                            e2.forEach((e3)=>{
                                let obj = {
                                    week:week,
                                    meal_times:x,
                                    food_id:e3.food_id || e3.id
                                };
                                list.push(obj)
                            })
                        });
                        if(list.length <= 0){
                            this.state = true;
                        }else{
                            this.state = false;
                        }
                    });
                }else{
                    this.state = true;
                }

                this.nFid = e;
                if(!this.state){
                    this.visible = !this.visible;
                }
                //  
               this.getFBook()
            },
            handleOk(){
                this.visible = !this.visible;
                this.ctnKey = this.nFid;
                this.getFBook();
                this.$message.success(``);
            },
            handleCancel(){
                this.visible = !this.visible;
                this.notData.forEach((e,i)=>{
                    e.name = [[],[],[],[],[],[],[]];
                });
                this.dataSource = this.notData;
                this.columns = this.hasTab;
            },
            back(){

//                if(this.fcList.length > 0){
//                    this.fcList.forEach((e,i)=>{
//                        let obj ={
//                            created_at:'',
//                            food_id: e.id,
//                            meal_times:'',
//                            title:e.name,
//                            use_time:'',
//                            week: this.foodI
//                        }
//                        this.foodL.push(obj);
//                    });
//                }else{
//
//                }
                this.addCK = !this.addCK;
            },
            bClick(val){
                this.btnI = val;
                this.seachFood(0);
            },
            fLiClick(val,id){
                this.sBtnI = val;
                this.seachFood(id);
            },
            all(){
                this.sBtnI = 0;
                this.btnI = 1;
                this.seachFood(0);
            },
            addFood(val,i,val2){
                this.fodList[i].state = !val2;
                if(this.foodL.length >=20){
                    this.$message.warning('');
                }else{
                    this.foodL.push(val)
                }
            },
            //
            reFod(val){
                this.foodL.splice(val, 1);
                this.fodList.forEach((e,i)=>{
                    e.state = false;
                    this.foodL.forEach((e2,i2)=>{
                        if(e.id == e2.id){
                            e.state = true;
                        }
                    });
                });
            },
            //2
            lookCk(val,val2,val3){
                this.fFood = val2;
                this.zFood = val3;
                this.visible2 = !this.visible2;
            },
            backC(){
                this.visible2 = !this.visible2;
            },
            addF(){
                this.sFood = !this.sFood
            },
            //
            check  () {
                const validateFieldsKey = ['fName','fNature', 'sFood','sFood2','upload']
                this.form.validateFields(validateFieldsKey,(err,values) => {
                        console.log(values);
                        if(values.upload == undefined){
                            this.rEShow2 = true
                        }else{
                            this.rEShow2 = false
                        }
                        if (!err) {

                        }

                    },
                );
            },
            //
            normFile  (e) {
                console.log('Upload event:', e);
                if (Array.isArray(e)) {
                    return e;
                }
                return e && e.fileList;
            },
            upImg(info) {
                if (info.file.status !== 'uploading') {
                    console.log(info.file, info.fileList);
                }
                if (info.file.status === 'done') {
                    this.$message.success(`${info.file.name} file uploaded successfully`);
                    this.rEShow2 = false
                } else if (info.file.status === 'error') {
                    this.$message.error(`${info.file.name} file upload failed.`);
                }
            },
            //
            v3Clisk(val){
                if(val == ''){
                    this.visible3 = !this.visible3;
                    let list = [];
                    if(this.dataSource[0].name[0] == ''){
                        this.$message.warning('');
                        return;
                    }else{
                        this.dataSource.forEach((e,i)=>{
                            let x = 0;
                            switch (i) {
                                case 0:
                                    x = 1;
                                    break;
                                case 1:
                                    x = 2;
                                    break;
                                case 2:
                                    x = 3;
                                    break;
                                case 3:
                                    x = 4;
                            }
                            e.name.forEach((e2,i2)=>{
                                let week = 0
                                switch (i2) {
                                    case 0:
                                        week = 1;
                                        break;
                                    case 1:
                                        week = 2;
                                        break;
                                    case 2:
                                        week = 3;
                                        break;
                                    case 3:
                                        week = 4;
                                        break;
                                    case 4:
                                        week = 5;
                                        break;
                                    case 5:
                                        week = 6;
                                        break;
                                    case 6:
                                        week = 7;
                                }
                                e2.forEach((e3)=>{
                                    let obj = {
                                        week:week,
                                        meal_times:x,
                                        food_id:e3.food_id || e3.id
                                    };
                                    list.push(obj)
                                })
                            });
                        });
                        let data = {
                            url:'api/school/recipes/'+ this.week.nOne + this.week.nSeven,
                            method:'put',
                            data:{
                                id:this.ctnKey,
                                producer:this.btmTxt.producer,
                                dietitian:this.btmTxt.dietitian,
                                remake:this.refuse,
                                foods:JSON.stringify(list)
                            }
                        };
                        this.$apiAxiox(data).then((res)=>{
                            if(res.data.code == 0){
                                this.$message.success(``);
                            }else{
                                this.$message.error(res.data.msg);
                            }
                        });
                    };
                }else if(val == ''){
                    this.visible3 = !this.visible3
                }else if(val == ''){
                    this.visible3 = !this.visible3
                }else{
                    this.visible3 = !this.visible3
                }
            },
            //3
            v3back(){
                this.visible3 = !this.visible3
            },
            //
            onChange(e){
                console.log(e[0].format("MMDD"));
                console.log(e[1].format("MMDD"));
//                console.log(e.format("MMDD"));
            },
            //4
            tBack(){
                this.visible4 = !this.visible4
            },
            //
            ckTime(){
                this.visible4 = !this.visible4
                this.$message.success(``);
            },
            //
            getPlist(){
                const validateFieldsKey = ['selectF','selectS','rpicker'];
                this.form2.validateFields(validateFieldsKey,(err,values) => {
                        console.log(values);
                        if (!err) {
                            this.ctnKey = this.rSList[values.selectF-1].id;
                            this.rlID = this.rList[values.selectS-1].id;
//                            console.log(this.rList[values.selectS - 1]);
                            if(values.rpicker != undefined){
                                this.time.s = moment(values.rpicker[0]._d).format('YYYYMMDD');
                                this.time.e = moment(values.rpicker[1]._d).format('YYYYMMDD');
                            }else{
                                this.time.s = '';
                                this.time.e = '';
                            }
                            this.getAllFoodBock(1);
                        }

                    },
                );
            },
            //
            clearL(){
                this.rState = 1;
                this.rstate = 1;
                this.form2.resetFields();
            },
            //
            handleTableChange(pagination, filters, sorter) {
                this.pagination.pageSize = pagination.pageSize;
                this.pagination.current = pagination.current;
                this.getAllFoodBock(1);
            },
            //
            open(e){
                this.isOpen = e;
                this.kVisible = true;
                if(this.oNum){
                    this.oTxt=''
                    this.openTxt = ''
                }else{
                    this.oTxt=''
                    this.openTxt = ''
                }
            },
            //
            kBack(){
                this.kVisible = !this.kVisible
            },
            //
            kOk(){
                this.kVisible = !this.kVisible
                this.oNum = !this.oNum
            },
            //
            newFList(){
                this.keyNum = "1";
            },
            //
            keyChange(e){
                this.keyNum = e;
                this.getAllFoodBock(0);
            },
            //
            kState(val,val2){
                this.sLoading = !this.sLoading;
                this.power = !this.power;
                if(val == 'NO'){
                    this.acShow = true
                }else{
                    this.acShow = false;
                };
                let data = {
                    url:'api/school/recipes/' + val2.code +'?id=' + val2.id,
                    method:'get',
                    data:{}
                };
                this.$apiAxiox(data).then((res)=>{
                    if(res.data.code == 0){
                        this.dCode = res.data.data.serial_num;
                        this.eData.name1 = res.data.data.dietitian;
                        this.eData.name2 = res.data.data.producer;
                        this.eData.name3 = res.data.data.state_str;
                        let data = {
                            key: '0',
                            logo:'',
                            name:[],
                        };
                        let data1 = {
                            key: '1',
                            logo:'',
                            name:[],
                        };
                        let data2 = {
                            key: '2',
                            logo:'',
                            name:[],
                        };
                        let data3 = {
                            key: '3',
                            logo:'',
                            name:[],
                        };
                        res.data.data.foods.forEach((e,i)=>{
                            data.name.push(e.meal_times[1]);
                            data1.name.push(e.meal_times[2]);
                            data2.name.push(e.meal_times[3]);
                            data3.name.push(e.meal_times[4]);
                        });
                        this.dataSourceS = [];
                        this.dataSourceS.push(data);
                        this.dataSourceS.push(data1);
                        this.dataSourceS.push(data2);
                        this.dataSourceS.push(data3);
                        this.sLoading = !this.sLoading;
                    };
                });
            },
            rOk(val){
                this.pVisible = !this.pVisible
                if(val == 'OK'){
                    this.pTitle = '';
                    this.pLder = '';
                    this.isState = 1;
                }else{
                    this.pTitle = '';
                    this.pLder = '';
                    this.isState = 2;
                }
            },
            //
            kback(){
                this.power = !this.power;
            },
            //
            pBack(){
                this.pVisible = !this.pVisible;
            },
            pOk(){
                let data = {
                    url:'api/school/examine/recipes',
                    method:'post',
                    data:{
                        serial_num: this.dCode,
                        feedback:this.pTxt,
                        state:this.isState
                    }
                };
                this.$apiAxiox(data).then((res)=>{
                    if(res.data.code == 0){
                        this.$message.success(``);
                        this.pTxt = '';
                        this.power = !this.power;
                        this.pVisible = !this.pVisible;
                        this.getAllFoodBock(0);
                    }else{
                        this.$message.error(res.data.msg);
                        this.pVisible = false;
                    }
                    return;
                });

            },
            //
            getData(){
                let data ={
                    url:'api/common/dictionaries',
                    method:'get',
                    data:{}
                };
                this.$apiAxiox(data).then((res)=>{
                    if(res.data.code == 0){
                        if(res.data.data.canteen.length >0){
                            this.ctnList = res.data.data.canteen;
                            this.ctnKey = this.ctnList[0].id;
                            this.rSList.push({id:0,title:''});
                            this.ctnList.forEach((e,i)=>{
                                this.rSList.push(e);
                            });
                        }else{
                            this.rState = '';
                        }
                        this.foodList = res.data.data.gener;
                        this.foodList.splice(0,0,{id:0,title:''});
                    }else{
                        this.$message.warning(res.data.msg);
                    }

                    this.getFBook();
                })
            },
            //
            getFBook(){
                this.loading = true;
                let data ={
                    url:'api/school/recipes/'+ this.week.nOne + this.week.nSeven + '?id=' + this.ctnKey,
                    //url:'api/school/recipes/'+ 2019090920190915 + '?id=' + 22,
                    method:'get',
                };
                this.$apiAxiox(data).then((res)=>{
                    if(res.data.code == 0){
                        if(res.data.data.length == undefined){
                            if(res.data.data.foods == []){
                                this.dataSource = this.notData;
                                this.columns = this.notTab;
                            }else{
                                if(res.data.data.state_str == ''){
                                    res.data.data.state_str = ''
                                    this.btnState = res.data.data.state_str;
                                }else{
                                    this.btnState = res.data.data.state_str;
                                }
                                this.btmTxt.dietitian = res.data.data.dietitian;
                                this.btmTxt.producer = res.data.data.producer;
                                this.btmTxt.name = res.data.data.auditor_nickname;
                                this.setData(res.data.data.foods);
                                this.isNew = false;
                            }
                            this.loading = false;
                            this.pageLoad = false;
                        }else{
                            this.dataSource = this.notData;
                            this.columns = this.notTab;
                            this.loading = false;
                            this.pageLoad = false;
                            this.isNew = true;
                            this.btnState = '';
                        }
                    }else{
                        this.isNew = true;
                        this.dataSource = this.notData;
                        this.columns = this.notTab;
                        this.$message.error(res.data.msg);
                        this.loading = false;
                        this.pageLoad = false;
                    }

                })
            },
            //
            getAllFoodBock(val){
                this.kTwoLoad = true;
                let that = this;
                let x = 0;
                if(val == 0){
                    this.pageLoad = true;
                    x = ''
                }else{
                    x = this.ctnKey
                }
                let data = {
                    url:'api/school/recipes?page=' + that.pagination.current +'&cid=' + x + '&state=' + that.rlID + '&begin=' + that.time.s + '&end=' + that.time.e,
                    method:'get',
                    data:{}
                };
                this.$apiAxiox(data).then((res)=>{
                    if(res.data.code == 0){
                        this.pagination.total = res.data.data.total;
                        this.dataSource2 = [];
                        res.data.data.data.forEach((e,i)=>{
                            //0,1,2
                            let x = '';
                            switch (e.state) {
                                case 0:
                                    x = '';
                                    break;
                                case 1:
                                    x = '';
                                    break;
                                case 2:
                                    x = '';
                            }
                            let obj = {
                                key: i,
                                droom:e.canteen_title,
                                time:e.begin_format +  ' - ' + e.end_format,
                                nName:e.dietitian,
                                rName:e.producer,
                                sTime:e.updated_at,
                                state:x,
                                code:e.cycle,
                                id:e.canteen_id
                            };
                            this.dataSource2.push(obj);
                        });
                        if(val == 0){
                            this.pageLoad = !this.pageLoad;
                        };
                        this.kTwoLoad = !this.kTwoLoad;
                    };
                })
            },
            //
            setData(dataList){
                let data = {
                    key: '0',
                    logo:'',
                    name:[],
                };
                let data1 = {
                    key: '1',
                    logo:'',
                    name:[],
                };
                let data2 = {
                    key: '2',
                    logo:'',
                    name:[],
                };
                let data3 = {
                    key: '3',
                    logo:'',
                    name:[],
                };
                this.isExpire = [];
                dataList.forEach((e,i)=>{
                    data.name.push(e.meal_times[1]);
                    data1.name.push(e.meal_times[2]);
                    data2.name.push(e.meal_times[3]);
                    data3.name.push(e.meal_times[4]);
                    this.isExpire.push(e.state);
                });
                console.log(this.isExpire);
                this.dataSource = [];
                this.dataSource.push(data);
                this.dataSource.push(data1);
                this.dataSource.push(data2);
                this.dataSource.push(data3);
                this.columns = this.hasTab;
            },
            //
            onpChange(e){
                this.pKey = e;
                this.seachFood(0);
            }
        },

        created(){
            this.getData();
            this.tFormat();
        },
        watch:{
            ctnKey:function(nV,oV){
                this.fID = oV;
            }
        }




    };
</script>
